import React from 'react';
import styled from 'styled-components';
import {size, palette} from 'styled-theme';
import {Link} from 'components';

const Wrapper = styled.nav `
ol {
  display: table;
  width: 100%;
  border-collapse: collapse;
  table-layout: fixed;
  text-align: center;
  white-space: nowrap;
}
a {
  li{
    overflow: hidden;
    display: table-cell;
    padding: 20px;
    text-overflow: ellipsis;
    color:${palette('basescale', 3)};
    font-weight: bold;
    cursor: pointer;
  }
  overflow: hidden;
  display: table-cell;
  padding: 20px;
  text-overflow: ellipsis;
  color:${palette('basescale', 3)};
  font-weight: bold;
  cursor: pointer;
  &.active{
    color:${palette('basescale', 5)};
    cursor: default;
  }
  &:hover{
    color:${palette('basescale', 5)};
  }
}
`
class MenuHorizontal extends React.Component {

  constructor(props) {
    super(props);
  }

  render() {

    return (
      <Wrapper>
        <ol>
          <Link to={`/file/summary/${this.props.id}`}><li className={(this.props.active === 'summary' ? 'active' : '')} >Resumen</li></Link>
          <Link to={`/file/detail/${this.props.id}`}><li className={(this.props.active === 'detail' ? 'active' : '')}>Detalles</li></Link>
          <Link to={`/file/multimedia/${this.props.id}`}><li className={(this.props.active === 'multimedia' ? 'active' : '')}>Multimedia</li></Link>
          <Link to={`/file/maps/${this.props.id}`}><li className={(this.props.active === 'maps' ? 'active' : '')}>Mapas</li></Link>
          <Link to={`/file/community/${this.props.id}`}><li className={(this.props.active === 'community' ? 'active' : '')}>Comunidad</li></Link>
          <Link to={`/file/literature/${this.props.id}`}><li className={(this.props.active === 'literature' ? 'active' : '')}>Literatura</li></Link>
          <Link to={`/file/data/${this.props.id}`}><li className={(this.props.active === 'data' ? 'active' : '')}>Datos</li></Link>
        </ol>
      </Wrapper>
    )
  }
}

export default MenuHorizontal;
